import { Favorite, MoreVert, ThumbUp } from "@mui/icons-material";
import { useState } from "react";
import "./post.css";

const Post = () => {
  const [like, setLike] = useState(21);
  const [isliked, setIsLiked] = useState(false);

const likeHander = () =>{
  setLike(isliked ? like-1 : like+1)
  setIsLiked(!isliked)
}

  return (
    <div className="post">
      <div className="postWraper">
        <div className="postTop">
          <div className="postTopLeft">
            <img src="/assets/hande.jpg" alt="" className="postProfileImg" />
            <span className="postUserName">Nishant Mishra</span>
            <span className="postDDate">5 min ago</span>
          </div>
          <div className="postTopRight">
            <MoreVert style={{cursor:"pointer"}} />
          </div>
        </div>
        <div className="postCenter">
          <span className="postText">Hey! Its my first post:)</span>
          <img src="/assets/hande.jpg" alt="" className="postImg" />
        </div>
        <div className="postBottom">
          <div className="postBottomLeft">
            <ThumbUp htmlColor="blue" className="likeIcon" onClick={likeHander} />
            <Favorite htmlColor="red" className="likeIcon" onClick={likeHander} />
            <span className="postLikeCounter">{like} people like</span>
          </div>
          <div className="postBottomRight">
              <span className="postCommentsText">9 comments</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Post;
